.roboto {
    font-family: $roboto;
    @include f(20px, 900, 0.9);
    color: $logo; }

.title-hr {
    position: relative;
    &:after {
        content: '';
        position: absolute;
        display: inline-block;
        width: 160px;
        height: 1px;
        background-color: $black;
        color: $black;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%); } }
.title-xl {
    @include f(46px, 700, 1);
    letter-spacing: 0.02em; }
.title-l {
    @include f(40px, 700, 1);
    letter-spacing: 0.02em;
    padding-bottom: 15px; }
.title-m {
    @include f(24px, 700, 1.25);
    letter-spacing: 0.02em; }
.title-s {
    @include f(20px, 700, 1.2);
    letter-spacing: 0.02em; }

.subtitle {
    @include f(24px, 400, 1);
    letter-spacing: 0.02em;
    margin-top: 23px; }

