html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "AvenirNextCyr";
  font-style: normal;
  font-weight: 400;
  src: local("AvenirNextCyr-Regular"), local("AvenirNextCyr-Regular"), url("../fonts/AvenirNextCyr-Regular.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Regular.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr";
  font-style: normal;
  font-weight: 500;
  src: local("AvenirNextCyr-Medium"), local("AvenirNextCyr-Medium"), url("../fonts/AvenirNextCyr-Medium.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Medium.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr";
  font-style: italic;
  font-weight: 500;
  src: local("AvenirNextCyr-MediumItalic"), local("AvenirNextCyr-MediumItalic"), url("../fonts/AvenirNextCyr-MediumItalic.woff2") format("woff2"), url("../fonts/AvenirNextCyr-MediumItalic.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr";
  font-style: normal;
  font-weight: 700;
  src: local("AvenirNextCyr-Bold"), local("AvenirNextCyr-Bold"), url("../fonts/AvenirNextCyr-Bold.woff2") format("woff2"), url("../fonts/AvenirNextCyr-Bold.woff") format("woff");
}

@font-face {
  font-family: "AvenirNextCyr";
  font-style: italic;
  font-weight: 700;
  src: local("AvenirNextCyr-BoldItalic"), local("AvenirNextCyr-BoldItalic"), url("../fonts/AvenirNextCyr-BoldItalic.woff2") format("woff2"), url("../fonts/AvenirNextCyr-BoldItalic.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  src: local("Roboto-Black"), local("Roboto-Black"), url("../fonts/Roboto-Black.woff2") format("woff2"), url("../fonts/Roboto-Black.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

.icon-plus {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 14px;
  font-family: "AvenirNextCyr", "Helvetica", sans-serif;
  font-weight: 400;
  color: #000;
  background-color: #fff;
  min-width: 320px;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  text-decoration: none;
}

.inner {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.block {
  display: block;
}

.block {
  display: flex;
}

.header {
  padding: 28px 0 25px;
  background-color: #180c0c;
  color: #fff;
}

.header__inner {
  display: flex;
  align-items: flex-start;
}

.header__logo {
  display: flex;
  align-items: flex-end;
  padding-right: 47px;
  padding-top: 5px;
}

.header__img {
  width: 54px;
}

.header__title {
  margin-left: 13px;
}

.header__caption {
  border-left: 2px solid #fff;
  padding: 10px 24px 0;
  margin-top: 15px;
  font-size: 22px;
  font-weight: 500;
  line-height: 1;
}

.header__right {
  margin-left: auto;
  padding-right: 7px;
}

.header__subtitle {
  color: #e1e0e0;
  line-height: 2.3;
  padding-left: 4px;
  letter-spacing: 0.02em;
  font-size: 20px;
}

.header__subtitle_sm {
  font-size: 16px;
  line-height: 1.9;
  letter-spacing: 0.02em;
}

.header__tel {
  font-size: 28px;
  font-weight: 700;
  line-height: 0.64;
}

.header__tel span {
  color: #949494;
}

.roboto {
  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 20px;
  font-weight: 900;
  line-height: 0.9;
  color: #cfbf52;
}

.title-hr {
  position: relative;
}

.title-hr:after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 160px;
  height: 1px;
  background-color: #000;
  color: #000;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}

.title-xl {
  font-size: 46px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.02em;
}

.title-l {
  font-size: 40px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.02em;
  padding-bottom: 15px;
}

.title-m {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0.02em;
}

.title-s {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.02em;
}

.subtitle {
  font-size: 24px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.02em;
  margin-top: 23px;
}

.main {
  color: #e1e0e0;
  text-align: center;
  height: 705px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0 30px;
}

.main:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.75);
}

.main__wrap {
  margin-top: -72px;
  z-index: 1;
}

.main__title {
  padding-bottom: 28px;
  position: relative;
}

.main__title:after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 354px;
  height: 1px;
  background-color: #fff;
  color: #fff;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
}

.main__subtitle {
  max-width: 500px;
  margin: 16px auto 0;
}

.main__btn {
  margin-top: 55px;
}

.btn {
  display: inline-block;
  border: none;
  color: #f8e461;
  text-decoration: none;
  background-color: transparent;
  padding: 10px 40px;
  font-size: 16px;
  font-size-line-height: 1.4;
  font-family: sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 4px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .3s, color .3s;
  cursor: pointer;
  cursor-font-family: "AvenirNextCyr", "Helvetica", sans-serif;
}

.btn:focus, .btn:hover {
  text-decoration: none;
}

.btn_border {
  border: 2px solid #f8e461;
  border-radius: 10px;
  min-width: 230px;
}

.no-touch .btn_border:hover {
  background-color: #f8e461;
  color: #000;
}

.choice {
  padding: 60px 0 80px;
}

.choice__title {
  text-align: center;
}

.choice__cards {
  margin: 70px -11px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.choice__item {
  padding: 0 11px;
}

.card-gray {
  max-width: 270px;
  background-color: #f7f7f7;
  border-radius: 20px;
  padding: 27px 19px 20px;
  min-height: 362px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-gray__img-wrap {
  min-height: 124px;
}

.card-gray__img {
  width: 100%;
  height: 100%;
}

.card-gray__title {
  margin-top: 15px;
  text-align: center;
  min-height: 60px;
}

.card-gray p {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  word-break: break-all;
  word-break: break-word;
  hyphens: auto;
  margin-top: 27px;
}

.card-gray p span {
  font-weight: 700;
}

.card-benefit {
  max-width: 233px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 20px;
  padding: 40px 40px 20px;
  box-shadow: 0 0 15px 10px #e8e8e8;
  min-height: 350px;
  display: flex;
  flex-direction: column;
}

.card-benefit__img-wrap {
  flex: 1 0 auto;
}

.card-benefit__title {
  margin-top: 24px;
  flex: 1 0 auto;
}

.card-benefit__text {
  color: #d85c5c;
  font-size: 16px;
  font-weight: 700;
  margin-top: 16px;
  flex: 0 0 auto;
}

.card-benefit__text span {
  font-size: 26px;
}

.benefit__item:last-child .card-benefit {
  padding: 40px 0 20px;
}

.benefit {
  padding: 60px 0 80px;
  background-color: #f2f2f2;
  text-align: center;
}

.benefit__cards {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.benefit__item {
  display: flex;
}

.benefit__item .icon {
  font-size: 44px;
  display: inline-block;
  width: 72px;
  margin-top: 100px;
}
