//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


// AvenirNextCyr
@font-face {
	@include fontdefault(normal, 400, 'AvenirNextCyr');
	@include fontpath('AvenirNextCyr-Regular', 'AvenirNextCyr-Regular', 'AvenirNextCyr-Regular'); }

@font-face {
	@include fontdefault(normal, 500, 'AvenirNextCyr');
	@include fontpath('AvenirNextCyr-Medium', 'AvenirNextCyr-Medium', 'AvenirNextCyr-Medium'); }

@font-face {
	@include fontdefault(italic, 500, 'AvenirNextCyr');
	@include fontpath('AvenirNextCyr-MediumItalic', 'AvenirNextCyr-MediumItalic', 'AvenirNextCyr-MediumItalic'); }

@font-face {
	@include fontdefault(normal, 700, 'AvenirNextCyr');
	@include fontpath('AvenirNextCyr-Bold', 'AvenirNextCyr-Bold', 'AvenirNextCyr-Bold'); }

@font-face {
	@include fontdefault(italic, 700, 'AvenirNextCyr');
	@include fontpath('AvenirNextCyr-BoldItalic', 'AvenirNextCyr-BoldItalic', 'AvenirNextCyr-BoldItalic'); }

// ROBOTO

@font-face {
	@include fontdefault(normal, 900, 'Roboto');
	@include fontpath('Roboto-Black', 'Roboto-Black', 'Roboto-Black'); }
